"use client";

import { useState, useEffect } from "react";
import { Heart, Clock, CheckCircle, AlertCircle, MapPin, Settings, LogOut } from "lucide-react";
import Link from "next/link";
import { jwtDecode } from "jwt-decode";
import { REQUEST } from "@/lib/api-endpoints";
import { useSocket } from "@/hooks/useSocket";

export default function RecipientDashboard() {
  const [location, setLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [recipientId, setRecipientId] = useState<string | null>(null);
  const [requests, setRequests] = useState([]);
  const [selectedRequest, setSelectedRequest] = useState<any | null>(null);
  const [showDetailsModal, setShowDetailsModal] = useState(false);

  // ------------------------------
  // STEP 1: Decode JWT â†’ get recipientId
  // ------------------------------
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      try {
        const decoded: any = jwtDecode(token);
        if (decoded.role === "recipient") {
          setRecipientId(decoded.userId);
        }
      } catch (err) {
        console.error("Invalid token:", err);
      }
    }
  }, []);

  // ------------------------------
  // STEP 2: Get user location for nearby match features
  // ------------------------------
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((pos) => {
        setLocation({
          lat: pos.coords.latitude,
          lng: pos.coords.longitude,
        });
      });
    }
  }, []);

  // ------------------------------
  // STEP 3: Fetch Blood Requests From Backend
  // ------------------------------
  useEffect(() => {
    if (!recipientId) return;

    fetch(REQUEST.getAllRequests)
      .then((res) => res.json())
      .then((data) => {
        const allRequests = data.requests || [];

        // Filter only requests made by THIS recipient
        const myRequests = allRequests.filter(
          (req: any) => req.requestedBy === recipientId
        );

        setRequests(myRequests);
      })
      .catch((err) => console.error("Request fetch error:", err));
  }, [recipientId]);

  // --------------------------------
  // Helpers for status design
  // --------------------------------
  const getStatusColor = (status: string) => {
    switch (status) {
      case "Fulfilled":
        return "text-green-600";
      case "Pending":
        return "text-orange-600";
      case "Cancelled":
        return "text-red-600";
      default:
        return "text-gray-600";
    }
  };

  const getStatusBgColor = (status: string) => {
    switch (status) {
      case "Fulfilled":
        return "bg-green-100";
      case "Pending":
        return "bg-orange-100";
      case "Cancelled":
        return "bg-red-100";
      default:
        return "bg-gray-100";
    }
  };

  return (
    <main className="min-h-screen bg-gradient-to-br from-white via-red-50 to-white">
      {/* Header */}
      <header className="bg-gradient-to-r from-red-600 to-red-700 text-white p-6">
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <div className="flex items-center gap-3">
            <Heart className="w-8 h-8 fill-white" />
            <h1 className="text-3xl font-bold">Pulse Bank - Recipient</h1>
          </div>

          <div className="flex items-center gap-4">
            {location && (
              <div className="flex items-center gap-2 bg-red-500 px-4 py-2 rounded-lg">
                <MapPin className="w-5 h-5" />
                <span className="text-sm">Location Active</span>
              </div>
            )}
            <button 
              onClick={() => {
                setSelectedRequest(req);
                setShowDetailsModal(true);
              }}
              className="p-2 hover:bg-red-500 rounded-lg transition">
              <Settings className="w-6 h-6" />
            </button>
            <Link href="/" onClick={() => {
              localStorage.removeItem("token");
              localStorage.removeItem("user");
            }} className="p-2 hover:bg-red-500 rounded-lg transition">
              <LogOut className="w-6 h-6" />
            </Link>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto p-6 space-y-8">
        {/* Welcome Section */}
        <div className="bg-white rounded-xl shadow-lg p-8 border-l-4 border-red-600">
          <h2 className="text-3xl font-bold mb-2">Your Blood Request Status</h2>
          <p className="text-gray-600">
            Our AI system is finding the best matches for you. Stay updated on your request progress below.
          </p>
        </div>

        {/* Button: New Request */}
        <div className="flex justify-between items-center">
          <h2 className="text-2xl font-bold">Your Blood Requests</h2>
          <Link
            href="/recipient/create-request"
            className="px-6 py-2 bg-red-600 text-white font-semibold rounded-lg hover:bg-red-700 transition"
          >
            + New Request
          </Link>
        </div>

        {/* Request Cards */}
        <div className="space-y-6">
          {requests.length === 0 && (
            <p className="text-gray-600">You have not created any requests yet.</p>
          )}

          {requests.map((req: any, i: number) => (
            <div
              key={i}
              className={`bg-white rounded-xl shadow-lg p-8 border-l-4 ${
                req.status === "Fulfilled"
                  ? "border-green-500"
                  : req.status === "Pending"
                  ? "border-orange-500"
                  : "border-red-500"
              }`}
            >
              <div className="grid md:grid-cols-3 gap-8">
                {/* Blood Info */}
                <div>
                  <h3 className="text-sm text-gray-600 font-semibold mb-2">
                    BLOOD TYPE NEEDED
                  </h3>
                  <div className="flex items-start gap-4">
                    <div className="text-5xl font-bold text-red-600">{req.bloodType}</div>
                    <div>
                      <p className="text-2xl font-bold">{req.unitsNeeded}</p>
                      <p className="text-sm text-gray-600">units</p>
                    </div>
                  </div>
                </div>

                {/* Status */}
                <div>
                  <h3 className="text-sm text-gray-600 font-semibold mb-2">STATUS</h3>
                  <div
                    className={`inline-flex items-center gap-2 px-4 py-2 rounded-lg ${getStatusBgColor(
                      req.status
                    )}`}
                  >
                    {req.status === "Fulfilled" ? (
                      <>
                        <CheckCircle className="w-5 h-5 text-green-600" />
                        <span className="font-bold text-green-600">Fulfilled</span>
                      </>
                    ) : (
                      <>
                        <Clock className="w-5 h-5 text-orange-600" />
                        <span className="font-bold text-orange-600">Pending</span>
                      </>
                    )}
                  </div>
                </div>

                {/* Actions */}
                <div className="flex flex-col gap-3 justify-center">
                  <button 
                    onClick={() => {
                      setSelectedRequest(req);
                      setShowDetailsModal(true);
                    }}
                    className="px-6 py-3 bg-red-600 text-white font-semibold rounded-lg hover:bg-red-700 transition">
                    View Details
                  </button>
                  <button 
                    onClick={() => {
                      setSelectedRequest(req);
                      setShowDetailsModal(true);
                    }}
                    className="px-6 py-3 bg-gray-100 text-gray-700 font-semibold rounded-lg hover:bg-gray-200 transition">
                    Track Progress
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* How It Works */}
        <div className="bg-gradient-to-r from-red-50 to-red-100 rounded-xl p-8 border-2 border-red-200">
          <h2 className="text-2xl font-bold mb-6">How AI Matching Works</h2>
          <div className="grid md:grid-cols-4 gap-6">
            {[
              { step: 1, title: "Request Submitted", desc: "Your blood request is registered." },
              { step: 2, title: "AI Analysis", desc: "System analyzes compatibility & proximity." },
              { step: 3, title: "Donor Match", desc: "Compatible donors identified." },
              { step: 4, title: "Delivery", desc: "Blood prepared & delivered." },
            ].map((item) => (
              <div key={item.step} className="bg-white rounded-lg p-4">
                <div className="w-10 h-10 bg-red-600 text-white rounded-full flex items-center justify-center font-bold mb-3">
                  {item.step}
                </div>
                <h3 className="font-bold text-gray-900 mb-1">{item.title}</h3>
                <p className="text-sm text-gray-600">{item.desc}</p>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Details Modal */}
      {showDetailsModal && selectedRequest && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-xl shadow-2xl max-w-2xl w-full max-h-96 overflow-y-auto">
            <div className="bg-gradient-to-r from-red-600 to-red-700 text-white p-6">
              <h2 className="text-2xl font-bold">Request Details</h2>
            </div>
            <div className="p-8 space-y-6">
              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <h3 className="text-gray-600 font-semibold text-sm mb-2">BLOOD TYPE</h3>
                  <p className="text-3xl font-bold text-red-600">{selectedRequest.bloodType}</p>
                </div>
                <div>
                  <h3 className="text-gray-600 font-semibold text-sm mb-2">UNITS NEEDED</h3>
                  <p className="text-3xl font-bold">{selectedRequest.unitsNeeded}</p>
                </div>
                <div>
                  <h3 className="text-gray-600 font-semibold text-sm mb-2">STATUS</h3>
                  <p className={`text-lg font-bold ${getStatusColor(selectedRequest.status)}`}>
                    {selectedRequest.status}
                  </p>
                </div>
                <div>
                  <h3 className="text-gray-600 font-semibold text-sm mb-2">URGENCY</h3>
                  <p className="text-lg font-bold text-orange-600">{selectedRequest.urgency || "MODERATE"}</p>
                </div>
                <div>
                  <h3 className="text-gray-600 font-semibold text-sm mb-2">HOSPITAL</h3>
                  <p className="text-lg font-bold">{selectedRequest.hospital || "N/A"}</p>
                </div>
                <div>
                  <h3 className="text-gray-600 font-semibold text-sm mb-2">DISTANCE</h3>
                  <p className="text-lg font-bold">{selectedRequest.locationKm || "0"} km</p>
                </div>
              </div>
              <div>
                <h3 className="text-gray-600 font-semibold text-sm mb-2">CREATED</h3>
                <p className="text-gray-700">{new Date(selectedRequest.createdAt || Date.now()).toLocaleString()}</p>
              </div>
              <div className="pt-4 border-t">
                <h3 className="text-gray-600 font-semibold text-sm mb-3">PROGRESS</h3>
                <div className="space-y-3">
                  <div className="flex items-center gap-3">
                    <div className={`w-4 h-4 rounded-full ${selectedRequest.status === "Fulfilled" ? "bg-green-600" : "bg-orange-400"}`}></div>
                    <span className="text-gray-700">Request {selectedRequest.status === "Fulfilled" ? "Fulfilled" : "Pending"}</span>
                  </div>
                  {selectedRequest.status === "Fulfilled" && (
                    <>
                      <div className="flex items-center gap-3">
                        <div className="w-4 h-4 rounded-full bg-green-600"></div>
                        <span className="text-gray-700">Donor Matched</span>
                      </div>
                      <div className="flex items-center gap-3">
                        <div className="w-4 h-4 rounded-full bg-green-600"></div>
                        <span className="text-gray-700">Blood Scheduled for Delivery</span>
                      </div>
                    </>
                  )}
                </div>
              </div>
            </div>
            <div className="bg-gray-50 p-6 border-t flex justify-end gap-3">
              <button
                onClick={() => setShowDetailsModal(false)}
                className="px-6 py-2 bg-gray-300 text-gray-800 font-semibold rounded-lg hover:bg-gray-400 transition"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </main>
  );
}
